import Swal from "sweetalert2";
import { fetchConToken, fetchSinToken } from "../helpers/fetch";
import { types } from "../types/types";


export const startLogin = (email, password) => {
    return async(dispatch) => {

        const result = await fetchSinToken('auth', {email, password}, 'POST');
        const body = await result.json();
        if(body.ok){
            const {token, uid, name} = body;
            localStorage.setItem('token', token);
            localStorage.setItem('token-init-date', new Date().getTime());

            dispatch(login(uid, name));
        }else{
            Swal.fire('ERROR', 'Email o password incorrecto', 'error')
        }
    }
}

const login = (id, name) => {
    return {
        type: types.authLogin,
        payload: {id, name}
    }
}

export const startRegister = (name, email, password) => {
    return async (dispatch) => {
        const result = await fetchSinToken('auth/new', {name, email, password}, 'POST');
        const body = await result.json();
        if(body.ok){
            const {token, uid, name} = body;
            localStorage.setItem('token', token);
            localStorage.setItem('token-init-date', new Date().getTime());

            dispatch(register(uid, name));
        }else{
            Swal.fire('Error', body.msg , 'error')
        }
    }
}

const register = (id, name) => {
    return {
        type: types.authRegister,
        payload: {id, name}
    }
}

export const startChecking = () => {
    return async (dispatch) => {
        const result = await fetchConToken('auth/renew');
        const body = await result.json();
        if(body.ok){
            const {token , uid, name } = body;
            localStorage.setItem('token', token);
            localStorage.setItem('token-init-date', new Date().getTime());

            dispatch(login(uid, name));
        }else{
            dispatch(finishChecking())
        }
    }
}

const finishChecking = () => {
    return {
        type: types.authCheckingFinish
    }
}

export const startLogout = () => {

    localStorage.clear();

    return {
        type: types.authLogout
    }
}