import React, { useEffect, useState } from 'react'
import {Calendar, momentLocalizer} from 'react-big-calendar'
import 'react-big-calendar/lib/css/react-big-calendar.css'
import moment from 'moment'
import Navbar from '../ui/Navbar'

import './CalendarScreen.css'
import 'moment/locale/es'
import { messages } from '../../helpers/calendar-messages-es'
import CalendarEvent from './CalendarEvent'
import CalendarModal from './CalendarModal'
import { useDispatch, useSelector } from 'react-redux'
import { startOpenModal } from '../../actions/ui'
import { startClearActive, startEventsLoad, startSetActive } from '../../actions/event'
import FabAddNew from '../ui/FabAddNew'
import FabDeleteEvent from '../ui/FabDeleteEvent'


moment.locale('es');

const localizer = momentLocalizer(moment);

const CalendarScreen = () => {

    const [lastView, setLastView] = useState(localStorage.getItem('lastView') || 'month');
    const dispatch = useDispatch();
    const {events, activeEvent} = useSelector(state => state.calendar);
    const {user} = useSelector(state => state.auth)
    
    useEffect(() => {
        dispatch(startEventsLoad());
    }, [])

    const eventStyleGetter = (event) => {
        const style = {
            backgroundColor: (event.user.id === user.id) ? '#367CF7' : '#465660',
            borderRadius: '0px',
            display: 'block',
            opacity: '0.8',
            color: 'white'
        }

        return {
            style
        }
    }

    const openModal = () => {
        dispatch(startOpenModal())
    }

    const onSelected = (e) => {
        dispatch(startSetActive(e));
    }

    const onDoubleClick = (e) => {
        openModal();
    }

    const onViewChange = (e) => {
        setLastView(e);
        localStorage.setItem('lastView', e);
        console.log(e);
    }

    const onSelectSlot = (e) => {
        //console.log(e);
        dispatch(startClearActive())
    }

    return (
        <div className='calendar-screen'>
            <Navbar />
            <Calendar 
                localizer={localizer}
                startAccessor='start'
                endAccessor='end'
                events={events}
                messages={messages}
                eventPropGetter={eventStyleGetter}
                components={{
                    event: CalendarEvent
                }}
                onSelectEvent={onSelected}
                onDoubleClickEvent={onDoubleClick}
                onView={onViewChange}
                view={lastView}
                selectable={true}
                onSelectSlot={onSelectSlot}
            />
            <FabAddNew />
            {
                activeEvent && 
                <FabDeleteEvent />
            }
           
            <CalendarModal />
        </div>
    )
}

export default CalendarScreen
