import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { startClearActive, startEventDeleted } from '../../actions/event'
import './FabDeleteEvent.css'

const FabDeleteEvent = () => {
    const dispatch = useDispatch();
    const {activeEvent} = useSelector(state => state.calendar);

    const handleClick = (e) => {
        dispatch(startEventDeleted(activeEvent))
        dispatch(startClearActive());
    }
    return (
        <button
            onClick={handleClick}
            className='btn btn-danger fabDanger'>
            <i className='fas fa-trash'></i>
            <span>Eliminar</span>
        </button>
    )
}

export default FabDeleteEvent
