import moment from 'moment';
import React, { useEffect, useState } from 'react'
import { Button, Form } from 'react-bootstrap';
import Modal from 'react-modal';
import DateTimePicker from 'react-datetime-picker'
import './CalendarModal.css'
import Swal from 'sweetalert2';
import { useDispatch, useSelector } from 'react-redux';
import { startCloseModal } from '../../actions/ui';
import { startEventAddNew, startEventUpdated } from '../../actions/event';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
    },
};

// Make sure to bind modal to your appElement (https://reactcommunity.org/react-modal/accessibility/)
Modal.setAppElement('#root');

const now = moment().minutes(0).seconds(0).add(1, 'hours');
const nowplus = now.clone().add(1, 'hours');

const initialFormValues = {
    title: '',
    notes: '',
    start: now.toDate(),
    end: nowplus.toDate()
}

const CalendarModal = () => {

    const [titleValid, setTitleValid] = useState(true);
    const [formValues, setFormValues] = useState(initialFormValues);

    const {modalIsOpen} = useSelector(state => state.ui);
    const dispatch = useDispatch();
    const {activeEvent} = useSelector(state => state.calendar);

    const { title, notes, start, end } = formValues;


    useEffect(()=> {
        if(activeEvent){
            setFormValues(activeEvent);
        }else{
            setFormValues(initialFormValues);
        }
    },[activeEvent])

    function afterOpenModal() {
        // references are now sync'd and can be accessed.

    }

    function closeModal() {
        dispatch(startCloseModal());
        //setFormValues(initialFormValues);
        setTitleValid(true);
    }

    const handleChangeDateStart = (e) => {
        setFormValues(prev => ({
            ...prev,
            start: e
        }))
    }

    const handleChangeDateEnd = (e) => {
        setFormValues(prev => ({
            ...prev,
            end: e
        }))
    }

    const handleInputChange = (e) => {
        const { target } = e;
        setFormValues(prev => ({
            ...prev,
            [target.name]: target.value
        }))
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        
        const momentStart = moment(start);
        const momentEnd = moment(end);

        if(momentStart.isSameOrAfter(momentEnd)){
            Swal.fire('Error', 'La fecha final debe ser mayor a la inicial', 'error');
            return;
        }

        if(title.trim().length === 0) {
            setTitleValid(false);
            return;
        }

        if(activeEvent){
            dispatch(startEventUpdated(formValues))
        }else {
            dispatch(startEventAddNew(formValues))
        }

        closeModal();
        setFormValues(initialFormValues);
    }

    return (
        <Modal
            isOpen={modalIsOpen}
            onAfterOpen={afterOpenModal}
            onRequestClose={closeModal}
            style={customStyles}
            className='modal'
            overlayClassName='modal-fondo'
            closeTimeoutMS={200}
        >
            <h1> { activeEvent ? 'Editar Evento' : 'Nuevo Evento'} </h1>
            <hr />
            <Form onSubmit={handleSubmit}>
                <Form.Group className="mb-3">
                    <Form.Label>Fecha y hora inicio</Form.Label>
                    <DateTimePicker className='form-control' onChange={handleChangeDateStart} value={start} />
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Fecha y hora fin</Form.Label>
                    <DateTimePicker className='form-control' onChange={handleChangeDateEnd}
                        value={end}
                        minDate={start}
                        
                    />
                </Form.Group>

                <hr />

                <Form.Group className="mb-3">
                    <Form.Label>Titulo y notas</Form.Label>
                    <Form.Control type="text" placeholder="Titulo"
                        isInvalid={!titleValid}
                        name='title'
                        onChange={handleInputChange}
                        value={title}
                    />
                    <Form.Text className="text-muted">
                        Una descripción corta.
                    </Form.Text>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Control
                        as='textarea' rows={5} placeholder='Notas'
                        name='notes'
                        onChange={handleInputChange}
                        value={notes}
                    />
                    <Form.Text className="text-muted">
                        Información adicional.
                    </Form.Text>
                </Form.Group>

                <div className="d-grid gap-2">
                    <Button variant="outline-primary" type="submit" >
                        <i className="far fa-save"></i>
                        <span> Guardar</span>
                    </Button>
                </div>
            </Form>
        </Modal>
    )
}

export default CalendarModal
